  "),_c('br'),_vm._v("七、我们如何处理未成年人的个人信息\n                "),_c('br'),_vm._v("八、您的个人信息如何在全球范围转移\n                "),_c('br'),_vm._v("九、本政策如何更新\n                "),_c('br'),_vm._v("十、如何联系我们\n                "),_c('br'),_vm._v("一、定义与解释\n                "),_c('br'),_vm._v("1. 个人信息\n                "),_c('br'),_vm._v("个人信息是指以电子或者其他方式记录的能够单独或者与其他信息结合识别自然人个人身份的各种信息，包括但不限于自然人的姓名、出生日期、身份证件号码、个人生物识别信息、住址、电话号码。为免疑义，个人信息包括但不限于个人敏感信息。\n                2. 个人敏感信息\n                "),_c('br'),_vm._v("个人敏感信息是指一旦泄露、非法提供或滥用可能危害人身和财产安全，极易导致个人名誉、身心健康受到损害或歧视性待遇的个人信息。个人敏感信息包括但不限于身份证件号码、个人生物识别信息、银行账户、通信内容、健康生理信息。\n                3. 个人信息主体\n                "),_c('br'),_vm._v("个人信息主体指个人信息所标识的自然人\n                "),_c('br'),_vm._v("4. 去标识化\n                "),_c('br'),_vm._v("去标识化指通过对个人信息的技术处理，使其在不借助额外信息的情况下，无法识别个人信息主体的过程。\n                "),_c('br'),_vm._v("5. 匿名化\n                "),_c('br'),_vm._v("匿名化指通过对个人信息的技术处理，使得个人信息主体无法被识别，且处理后的信息不能被复原的过程。\n                "),_c('br'),_vm._v("6. 设备\n                "),_c('br'),_vm._v("设备是指可用于访问我们服务的装置，例如台式计算机、笔记本电脑、平板电脑或智能手机。\n                "),_c('br'),_vm._v("7. 唯一设备标识符\n                "),_c('br'),_vm._v("唯一设备标识符（专属 ID 或 UUID）是指由设备制造商编入到设备中的一串字符，可用于以独有方式标识相应设备（移动设备的 IMEI 号）。唯一设备标识符有多种用途，包括可在不能使用cookies时用以提供广告。\n                "),_c('br'),_vm._v("8. IP地址\n                "),_c('br'),_vm._v("每台上网的设备都会指定一个编号，称为互联网协议 (IP) 地址。这些编号通常都是根据地理区域指定的。IP 地址通常可用于识别设备连接至互联网时所在的位置。\n                "),_c('br'),_vm._v("9. GPS\n                "),_c('br'),_vm._v("Global Positioning System（全球定位系统）的简称。您使用的设备上的GPS信号接收器可以从GPS卫星收到信号并利用传来的信息计算您的位置。\n                "),_c('br'),_vm._v("10. WIFI\n                "),_c('br'),_vm._v("Wi-Fi是一种允许电子设备连接到一个无线局域网（WLAN）的技术，通常我们所说的无线网络。\n                "),_c('br'),_vm._v("11. 基站\n                "),_c('br'),_vm._v("基站，即公用移动通信基站，是无线电台站的一种形式，是指在一定的无线电覆盖区中，通过移动通信交换中心，与移动电话终端之间进行信息传递的无线电收发信电台。\n                "),_c('br'),_vm._v("12. SSL\n                "),_c('br'),_vm._v("SSL（Secure Socket Layer）又称为安全套接层，是在传输通信协议（TCP/IP）上实现的一种安全协议。SSL支持各种类型的网络，同时提供三种基本的安全服务，均通过使用公开密钥和对称密钥技术以达到信息保密的效果。\n                "),_c('br'),_vm._v("13. 信息收集技术\n                "),_c('br'),_vm._v("本政策中所述的“自动收集”包括以下方式：\n                "),_c('br'),_vm._v("1）Cookie\n                "),_c('br'),_vm._v("Cookie是您浏览网页时，网站服务器放在客户端（您的计算机、移动电话或其他智能终端内）里面的一个小小的文本文件，可用于改善您的使用体验。\n                "),_c('br'),_vm._v("2）Web beacon（网络信标）\n                "),_c('br'),_vm._v("Web beacon是装嵌在网站或电邮内的电子图像文件案或其他技术，可用于计算访客数目、记录您是否及何时阅览电邮或网站，或用以使用某些Cookie。\n                "),_c('br'),_vm._v("3）Log files（日志文件）\n                "),_c('br'),_vm._v("Log files储存自动收集的若干数据。该等数据包括互联网协议（IP）地址、浏览器类型、互联网服务提供商（ISP）、引用/退出页面、操作系统、日期/时间戳和点击流数据等。\n                "),_c('br'),_vm._v("4）ET Tag（实体标签）\n                "),_c('br'),_vm._v("ET Tag是在互联网浏览器与互联网服务器之间背后传送的HTTP协议标头，可代替Cookie，用以追踪个别使用者，使我们可更深入地了解和改善我们的服务。\n                "),_c('br'),_vm._v("5）JavaScript\n                "),_c('br'),_vm._v("JavaScript是一种编程语言，用于制作更具互动性和动态的网页。JavaScript可以设定Cookie、阅读Cookie及删除Cookie。\n                "),_c('br'),_vm._v("二、我们如何收集和使用您的个人信息\n                "),_c('br'),_vm._v("1、成为我们的用户并登录\n                本产品支持您使用手机号或通过第三方平台自行完成注册，成为我们的家长用户，也可使用由教师或学校管理人员进行创建并下发的学生、教师帐号进行登录。\n                在您登录后，您可以选择性地提供更多的个人信息，包括头像、昵称、性别、生日、所属区域、所在年级、身份，方便我们为您提供更好的服务。如您不提供上述额外信息，仍可以使用我们的产品或服务。\n                您提供的上述信息将在您使用我们提供的服务期间持续授权我们使用。在您主动注销帐号时，我们将根据适用法律法规的要求尽快使其匿名或删除您的个人信息。\n                "),_c('br'),_vm._v("2、为您提供我们的产品和服务\n                我们的产品主要面向学生、教师和家长，帮助学生和家长了解学习动态，提供教学资源。在学生端、教师端和家长端之间进行联动。\n                教师端的主要功能包括“首页”、“作业中心”、“班级管理”、“资源工具”、“学科圈”、“个人中心”、“教学教研”共7个主要业务功能。\n                学生端的主要功能包括“首页”、“作业中心”、“错题本”、“个人中心”、“资源工具”共4个主要业务功能。\n                教师端的主要是提供给学校教师的移动在线平台，针对教与学服务进行智能化分析，支持在线阅卷、看报告、试卷讲评等功能，包括利用网络在线布置作业，利用相机拍照功能上传试题和答案，利用消息通知发送班级动态\n                我们根据合法、正当、必要的原则，出于如下所述的目的收集和使用您的个人信息。\n                "),_c('br'),_vm._v("1）您通过我们的客服或参加我们举办的活动时所提交的信息\n                如果您参与我们的线上活动，填写的调查问卷中可能包含您的姓名、电话、电子邮箱、家庭地址信息。\n                "),_c('br'),_vm._v("2）您通过其他方式向我们所提交的信息\n                如果您通过邮寄或传真方式向我们提供相关的证明文件，文件中可能包含您的姓名、电话、家庭地址等信息。\n                如果您购买需要寄送的实体商品并向我们提供收货信息，收货信息中可能包含您的姓名、电话、家庭地址等信息。\n                "),_c('br'),_vm._v("3）设备信息\n                我们会根据您在软件安装及使用中授予的具体权限，接收并记录您所使用的设备相关信息，包括设备名称、设备型号、设备识别码、操作系统和应用程序版本、设备设置、电池、信号强度，语言设置、分辨率、设备传感器参数、服务提供商网络ID。\n                "),_c('br'),_vm._v("4）日志信息\n                当您使用我们的所有业务功能时，系统会通过cookies或其他方式自动收集某些信息作为有关网络日志保存。此类信息包括：使用本产品或服务时的详细使用情况，例如您的搜索查询内容、访问的页面地址、浏览器的类型、运营商、使用的语言、访问日期和时间、设备崩溃、系统活动、访问的网页记录以及用户触发事件统计数据等。该类信息将有助于我们统计分析学生的安全上网行为。\n                "),_c('br'),_vm._v("5）其他信息\n                在您使用我们的产品有疑惑时，在您与我们联系时，我们可能会保存您的通信/通话记录和语音等信息或您留下的联系方式等信息，以便与您联系或帮助您解决问题，或记录相关问题的处理方案及结果。\n                "),_c('br'),_vm._v("6）系统权限调用\n                "),_c('br'),_vm._v("为提供服务，我们会调用您的一些系统权限。下方我们列出了请求权限对应的业务功能、调用目的及请求时机。其中的所有权限，您都可以在系统的设置功能中选择关闭，关闭后爱课生将无法通过这些权限收集对应的个人信息。对于系统权限的具体操作方式，以您的设备系统开发者提供的说明为准。\n              ")])])],1)],1),_vm._v(" "),_c('div',{staticClass:"yeah"},[_c('el-button',{on:{"click":function($event){_vm.showBox=false}}},[_vm._v("不同意")]),_vm._v(" "),_c('el-button',{staticStyle:{"background-color":"#098f81","color":"#fff","border":"0"},on:{"click":_vm.agree}},[_vm._v("同意")])],1)]):_vm._e()])]),_vm._v(" "),_c('div',{staticClass:"articles"},[_c('Articles')],1),_vm._v(" "),_c('p',{staticClass:"copyright tc"},[_c('Footers')],1)])}
